* {
    box-sizing: border-box;
}

body {
    background: 
    linear-gradient(
        -53deg,
        rgb(255, 0, 242),
        navy);
    margin: 0;
    min-height: 100vh;
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 80vh;
    background: linear-gradient(
        -53deg,
        rgb(255, 0, 242),
        navy)
}

h1{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    top: 50px;
    color: whitesmoke;
}

.display{
    display: flex;
    flex-wrap: wrap;
    width: 400px;
    background: 
    linear-gradient(
        -56768541321323476513567468325746513437deg,
        navy,
        rgb(255, 0, 242),
        navy,
        rgb(255, 0, 242));
    border-radius: 4px;
    padding: 20px;
    box-shadow: 3px 1px 40px 30px rgb(0, 0, 0);
    margin: 0;
    justify-content: center;
    align-items: center;
}

.box{
    border: 0px solid rgb(243, 243, 243);
    border-radius: 4px;
    height: 100px;
    width: 100px;
    margin: 10px;
    font-size: 40px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
}

                    /* Hover Effects */

#box{
    position: relative;
    display: inline-flex;
    padding: 15px 30px;
    color: whitesmoke;
    letter-spacing: 4px;
    text-decoration: none;
    font-size: 20px;
    overflow: hidden;
    transition: 1s;
    text-align: center;
    justify-content: center;
    align-items: center;
}

#box:hover{
    color: whitesmoke;
    background: 
    linear-gradient(
        -53deg,
        navy,
        rgb(255, 0, 242)
        );
    box-shadow: 0 0 100px whitesmoke, 0 0 200px whitesmoke, 0 0 500px whitesmoke;
    transition-delay: 1.25s;
}

.active{
    color: whitesmoke;
    background: 
    linear-gradient(
        -53deg,
        rgb(255, 0, 242),
        navy
        );
    box-shadow: 0 0 100px whitesmoke, 0 0 200px whitesmoke, 0 0 500px whitesmoke;
    transition-delay: 1.25s;
}


#box span{
    position: absolute;
    display: block;
}

#box span:nth-child(1){
    top: 0;
    left: -100%;
    width: 100%;
    height: 2px;
    background: 
    linear-gradient(90deg,transparent,whitesmoke);
}

#box:hover span:nth-child(1){
    left: 100%;
    transition: 1s;
}

#box span:nth-child(3){
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: 
    linear-gradient(270deg,transparent,whitesmoke);
}

#box:hover span:nth-child(3){
    right: 100%;
    transition: 1s;
    transition-delay: 0.5s;
}


#box span:nth-child(2){
    top: -100%;
    right: 0;
    width: 2px;
    height: 100%;
    background: 
    linear-gradient(180deg,transparent, whitesmoke);
}

#box:hover span:nth-child(2){
    top: 100%;
    transition: 1s;
    transition-delay: 0.25s;
}

#box span:nth-child(4){
    bottom: -100%;
    left: 0;
    width: 2px;
    height: 100%;
    background: 
    linear-gradient(360deg,transparent,whitesmoke);
}

#box:hover span:nth-child(4){
    bottom: 100%;
    transition: 1s;
    transition-delay: 0.75s;
}